.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  position: relative;
  top: -44px;
  right: 0;
  padding-top: 134px;
  max-width: 320px;
}
@media (min-width: 1340px) {
  .main-nav__list {
    display: block;
  }
}

.main-nav__item a {
  display: block;
  padding: 17px 0 17px 16px;
  font-weight: 600;
  font-size: 20px;
  line-height: 28px;
  color: #48494D;
  background-color: #FBF8F8;
  margin-bottom: 30px;
}

.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  border: none;
  background-color: #FE7865;
  width: 50px;
  height: 50px;
}
@media (min-width: 1340px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  width: 24px;
  height: 1px;
  background-color: #ffffff;
  box-shadow: 0 8px 0 0 #ffffff, 0 16px 0 0 #ffffff;
  top: 15px;
  right: 11px;
}

.main-nav--closed .main-nav__list {
  display: none;
}

.main-nav--opened .main-nav__list::before, .main-nav--opened .main-nav__list::after {
  content: "";
  position: absolute;
  width: 25px;
  height: 2px;
  background-color: rgba(44, 46, 63, 0.2);
  right: 10px;
  top: 10px;
}
.main-nav--opened .main-nav__list::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__list::after {
  transform: rotate(-45deg);
}

